
@import url("https://fonts.googleapis.com/css?family=Merriweather");
html {
  box-sizing: border-box;
  font-size: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html, body, ul, ol, li, form, fieldset, legend {
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}
fieldset, img {
  border: 0;
}
legend {
  color: #000;
}
li {
  list-style: none;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption, th, td {
  text-align: left;
  vertical-align: top;
  font-weight: normal;
}
input, textarea, select {
  font-size: 110%;
  line-height: 1.1;
}
abbr, acronym {
  border-bottom: .1em dotted;
  cursor: help;
}
h1, h2, h3,
.tagline,
.nav {
  font-family: Merriweather, serif;
}
body {
  background-color: #F0EFEB;
}
.container {
  overflow: hidden;
}
.photo {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-attachment: fixed;
  background-image: url("../../static/img/mt-shuksan.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 80vh;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.hero {
  background: rgba(29, 48, 90, 0.5);
  color: rgba(255, 255, 255, 0.9);
  padding: 2em;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.hero[style*="position: fixed"] {
  font-size: 55%;
  left: 0 !important;
}
.title {
  font-size: 3em;
  margin: 0 0 0.2em;
}
.tagline {
  font-size: 1.5em;
}
.content {
  background-color: #F0EFEB;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.nav-container {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 230px;
          flex: 1 0 230px;
}
.nav {
  margin: 5em 0 0 1em;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  top: 60px;
}
.nav a {
    display: block;
    text-decoration: none;
    padding: 0.5em;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
}
.nav a:link {
      border-bottom: 2px solid transparent;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.9);
}
.nav a:visited {
      color: rgba(0, 0, 0, 0.9);
}
.nav[style*="position: fixed"] {
    left: 0 !important;
}
.section {
  margin: 5em 0 0;
  padding: 0 10vw 0 0;
}



